<template>
  <div id="Software">
    <el-row v-for="(rowData, index) in productData" :key="index">
      <el-col :span="7" v-for="(item, index) in rowData" :key="index" :offset="index > 0 ? 1 : 0">
        <el-card :body-style="{ padding: '0px' }" style="height:230px;margin-bottom:20px;">
          <img :src="item.img" class="image" />
          <div style="padding: 5px;">
            <router-link style="margin: 7px;" slot="reference" :to="item.path">{{item.name}}</router-link>
            <!-- <div style="margin: 7px;">{{item.name}}</div> -->
            <!-- <div class="bottom clearfix"> -->
            <!-- <el-button type="text" class="button" @click="jumpDetail(item)">查看详情</el-button> -->
            <!-- </div> -->
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { hukeData } from "../json/huke";

export default {
  name: "Product_Huke",
  data() {
    return {
      productData: hukeData
    };
  }
};
</script>
<style scoped>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 10px;
  line-height: 12px;
}

.button {
  padding: 0 10px;
  float: right;
}

.image {
  width: 100%;
  display: block;
  height: 190px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
</style>




